.header max {
  z-index: 20; }

.headerContent {
  height: 100vh;
  width: 100%;
  position: absolute;
  display: flex;
  align-items: center;
  z-index: 5; }

.logo {
  max-width: 500px;
  height: auto;
  margin-left: -35px;
  object-fit: contain; }

.headerTitle {
  display: flex;
  margin-bottom: 15vh;
  width: fit-content; }

.headerTitle h1 {
  margin: 0;
  max-width: 450px;
  font-weight: 100;
  font-size: 50px;
  padding: 30px;
  padding-left: 50px;
  border-radius: 2px 0 0px 2px;
  background-color: rgba(0, 52, 89, 0.9);
  color: white; }

.headerTitleContact {
  max-width: 450px;
  padding: 30px;
  padding-right: 50px;
  background-color: white;
  display: flex;
  align-items: center;
  color: #3c3c3c; }

.headerTitleContact ul {
  padding: 0;
  margin: 0; }

.headerTitleContact li {
  list-style-type: none;
  word-break: break-word;
  font-size: 14px;
  font-weight: 600;
  padding: 5px 0;
  display: flex;
  align-items: center; }

.mailLink {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  list-style-type: none !important;
  word-break: break-word !important;
  font-size: 14px !important;
  font-weight: 600 !important;
  padding: 5px 0 !important;
  display: flex !important;
  align-items: center !important; }

.headerTitleContact li b, .headerTitleContact li a {
  margin-right: 5px;
  font-weight: 400;
  text-decoration: none;
  color: #1d1d1d; }

.headerTitleContact li a:hover {
  color: #00528c; }

.headerTitle > a {
  background-color: rgba(0, 52, 89, 0.9);
  padding-right: 15px;
  border-radius: 0px 2px 2px 0px; }

.blurHider {
  width: 100%;
  height: 140px;
  background-color: #00528c;
  z-index: 40;
  position: absolute; }

.cardsContainer {
  padding-top: 140px;
  padding-bottom: 140px;
  background: none;
  width: 100%;
  z-index: -5;
  display: flex;
  flex-flow: column wrap; }

@media (max-width: 1250px) {
  .header > max {
    display: flex;
    justify-content: center; }
  .headerTitle h1 {
    max-width: 350px;
    font-size: 40px; } }

@media (max-width: 1050px) {
  .headerTitle {
    width: 100%; }
  .headerTitle h1 {
    max-width: fit-content; }
  .headerTitleContact {
    max-width: fit-content; }
  .headerTitleContact li {
    display: flex;
    flex-flow: row nowrap;
    align-items: flex-start; }
  .headerTitleContact li, .headerTitleContact li a, .headerTitleContact li b {
    word-break: keep-all;
    hyphens: none; } }

@media (max-width: 850px) {
  .headerTitle {
    flex-flow: row wrap;
    margin-bottom: 70px; }
  .headerTitle h1 {
    order: 1;
    width: 320px;
    padding: 30px 40px;
    border-radius: 2px 0 2px 0px; }
  .headerTitleContact {
    order: 4;
    max-width: none;
    width: 370px;
    padding: 30px 40px;
    border-radius: 0 0 2px 2px; }
  .headerTitle > a {
    order: 2;
    display: flex;
    justify-content: center;
    width: 50px;
    padding-top: 10px;
    padding-right: 0;
    border-radius: 0 2px 0 0; }
  .headerTitle > a > svg {
    margin: 0; }
  .headerTitle::before {
    content: '';
    display: block;
    width: 600px;
    height: 0px;
    order: 3; }
  .headerTitleContact li {
    display: flex;
    flex-flow: row nowrap;
    align-items: flex-start; }
  .headerTitleContact li, .headerTitleContact li a, .headerTitleContact li b {
    word-break: keep-all;
    hyphens: none; }
  .cardsContainer {
    padding: 50px 0; } }

@media (max-width: 700px) {
  .headerTitle {
    justify-content: center; }
  .headerTitle h1 {
    width: calc(100% - 2*40px - 50px);
    max-width: none; }
  .headerTitleContact {
    width: calc(100% - 2*40px); } }

@media (max-width: 550px) {
  .headerTitle {
    justify-content: center; }
  .headerTitle h1 {
    padding: 30px 20px;
    width: calc(100% - 2*20px - 50px);
    font-size: 30px; }
  .headerTitleContact {
    padding: 30px 20px;
    width: calc(100% - 2*20px); } }

@media (max-width: 400px) {
  .headerTitle h1 {
    font-size: 25px; }
  .headerTitleContact li {
    font-size: 14px; } }
